@import url(//use.fontawesome.com/releases/v5.15.3/css/all.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #292C36;
}

h1{
  color: #fff;
  font-size: 50px;
  margin-left: 30%;

}

h3 {
  color: #fff;
  margin-left: 27%;
}

.app {
  position: fixed;
  left: 50%; 
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #515865;
  border-radius: 5px;
  box-shadow: 2.8px 2.8px 2.8px 2.8px #4E668A;
  width: 25%;
  padding-bottom: 25px;
}

.btns {
  margin-left: 35%;
}

.btns button {
  padding: .5em;
  border: none;
  margin-right: 10px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: .5em;
}

.photo {
  height: 126px;
  width: 126px;
  border: 0;
  border-radius: 50%;
  margin-left: 33%;
  margin-top: 5%;
}

.like {
  margin-left: 45%;
  margin-top: 10px;
}

.social {
  margin-left: 46%;
  margin-top: 20px;
}

.fa-thumbs-up {
  color: #fff;
  cursor: pointer;
}

.fa-github {
  color: #fff;
  /* font-size: 100%; */
}

/* MEDIA QUERY */

@media screen and (min-width: 600px) {
  body{
    max-width: 100%;
  }
  .app {
    max-width: 100%;
    height: 425px;
  }
  .btns {
    padding: 0;
  }
  h1 {
    font-size: 45px;
    margin-left: 32%;
    margin-right: 10px;
  }
  .photo {
    max-width: 150px;
    margin-left: 32%;
    margin-right: 10px;
  }
}

@media screen and (max-width: 450px) {
  .btns {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0;
  }
  h1 {
    font-size: 25px;
    margin-left: 5px;
  }
  h3 {
    margin-left: 5px;
    font-size: 15px;
  }
  .photo {
    margin-left: 15px;
    height: 62px;
    width: 62px;
  }
  .btns button {
    margin: 3px;
  }
}
